import React from 'react';

let data=[];
const shoucangDecer=(state=data,action)=>{
    switch(action.type){
       
        case 'ADD_ITEM':
            return [...state,action.item];
        case 'DELETER_ITEM':
            //利用id删除list中的列表
            let arr=[...state];
            arr.splice(action.index,1);
            return arr;
        default:
            return state;
    }

}
export default shoucangDecer;