import {createStore,combineReducers,applyMiddleware,compose} from 'redux';
//将所有的reducers整合到一块,得到的是一个整体
import detailsDecer from './detailsDecer';
import loginDecer from './loginDucer';
import shoucang from './shoucangDecer';
import dataReducer from './dataReducer';


//当那个状态组件过于多的时候，我们需要使用combineReducers将他们整合为一个大的组件
let rootReducer=combineReducers({
    detailsDecer:detailsDecer,
    userinfo:loginDecer,
    shoucang:shoucang,
    datalist:dataReducer
});

//store相对与是一个公共的大的全局变量
const store=createStore(
    rootReducer,
    );

export default store;