import React from "react";
import 'antd-mobile/dist/antd-mobile.css';
import { Flex, WhiteSpace } from 'antd-mobile';
import { connect } from 'react-redux';
import {withRouter} from 'react-router-dom';
class Mylist extends React.Component {
  constructor(props) {
    super(props);
    this.state=({
      str:'iconfont icon-xingxing'
    })
  }
 
  addshouChan=()=>{
    const arr=this.props.shoucangList;
     console.log('shoucang list:',arr);
     const imgobj=this.props.imgObj;
     if(arr.length<1){
      this.props.dispatch({
        type:'ADD_ITEM',
        item:imgobj
      })
      //先收藏 再改变的
      this.setState({
        str:'iconfont icon-start2-red2'
      })
     }
     arr.map((item)=>{
      if(item.id==imgobj.id){
        alert('该商品已经被收藏了');
        this.setState({
          str:'iconfont icon-xingxing'
        })

      }else{
        //添加值到dispatch里面
        this.props.dispatch({
          type:'ADD_ITEM',
          item:imgobj
        })
        this.setState({
          str:'iconfont icon-start2-red2'
        })
      }
      
    })
  }
 
  addList=()=>{
    console.log('login userinfo:',this.props.user.userinfo);
    //先进行登录验证

    return (
      <div>
          {
             this.props.user.userinfo? <div>
                {
                  console.log('suceess'),
                  this.addshouChan()
                }
              </div>:<div>
                {

                  this.setState({
                    str:'iconfont icon-xingxing'

                  }),
                  this.props.history.push('/login')
                }
              </div>
          }
      </div>
  )
 }
  render() {

    return (
      <div style={{position: 'fixed',bottom:0, width: '100%', }}>
        <Flex style={{border:'1px #ccc solid'}}>
            <Flex.Item>
                <div>
                <i className="iconfont icon-dianpu"></i>
                <p>店铺</p> 
                </div>
            </Flex.Item>

            <Flex.Item>
            <div>
                <i className="iconfont icon-kefu"></i>
                <p>客服</p> 
                </div> 
            </Flex.Item>

            <Flex.Item onClick={this.addList}>
            <div>
                <i className={this.state.str}></i>
                <p>收藏</p> 
                </div>   
            </Flex.Item>
            <div style={{height:"35px",width:"100px",backgroundColor:"orange",borderRadius:'15px 0px 0px 15px',textAlign:'center'}}>
                <span style={{display:'inline-block',marginTop:"10px",color:'#fff',fontWeight:'bolder'}}>加入购物车</span>
            </div>
            <div style={{height:"35px",width:"100px",backgroundColor:"orangered",borderRadius:'0px 15px 15px 0px',textAlign:'center'}}>
               <span style={{display:'inline-block',marginTop:"10px",color:'#fff',fontWeight:'bolder'}}>立即购买</span>
            </div>
        
        </Flex>
      </div>
    );
}
}

const mapStateToProps=(state)=>({
  user:state.userinfo,
  shoucangList:state.shoucang
})
//connect方法里面的参数可以不用写，默认有
export default connect(mapStateToProps)( withRouter(Mylist));



