import React,{Fragment} from 'react';
import './content.css';
import Mylist from './Mylist';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';

//使用state的值 显示图片
class Content extends React.Component{
    constructor(){
        super();
        
    }
    backPage=()=>{
        console.log('go back props:',this.props);
        if(!this.props.userinfo){
            this.props.history.goBack();
        }else{
            this.props.history.push('/datalist');
        }
        
    }
    render(){
        console.log('userinfo context.js:',this.props.userinfo);
      
        console.log('content:',this.props.imgValue[0]);
       let obj=new Object(this.props.imgValue[0]);
       let str=''+obj.img;
       console.log(str);

        return (
            <Fragment>
                <div className="wap">
                    {/* 实现按钮部分 */}
                    <div className="container Imgbox" style={{paddingLeft:'0px',paddingRight:"0px"}}>
                        <p className="point" onClick={this.backPage}><span style={{color:"#fff",fontSize:"20px",marginLeft:"5px"}}>{`<`}</span></p>
                        <img  src={str} className="img-responsive"></img>
                    </div>
                <Mylist imgObj={this.props.imgValue[0]}/>
                </div>
                </Fragment>
       
        ) 
    }

}

const mapStateProps=(state)=>{
    return {
        imgValue:state.detailsDecer,
        userinfo:state.userinfo
    }
  }
  
export default connect(mapStateProps)(withRouter(Content));