import Password from 'antd/lib/input/Password';
import React from 'react';

import {connect} from 'react-redux';
const login=(props)=>{

    return <div style={{marginTop:'15%'}}>
        用户: <input type="text" placeholder="请输入用户名" style={{marginTop:'5%'}}></input><br></br>
        密码: <input type="password" placeholder="请输入密码" style={{marginTop:'10%'}}></input><br></br>
        <button type='submit' value='登录' onClick={()=>{
            //点击登录按钮，实现登录  跳转回到详情页 将登录useinfo dispath
            props.dispatch({
                type:'LOGIN',
                userinfo:'fumei'
              });
            //跳转到详情页:
            props.history.push('/datalist') ;
        }} style={{width:'30%',height:'20%',backgroundColor:'lightblue',marginLeft:'17%',marginTop:'10%'}}>登录</button>
     
    </div>
}

export default connect()(login);