import React from 'react';

//userinfo默认是没有登录的
const loginDucer=(state={userinfo:''},action)=>{
    switch(action.type){
       
        case 'LOGIN':
            return {userinfo:action.userinfo};
        case 'BACK':
            return {userinfo:''}
        default:
            return state;
    }

}
export default loginDucer;