import { NavBar, Icon } from 'antd-mobile';
import React from 'react';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import { Flex} from 'antd-mobile';

class Doc extends React.Component{
  constructor(){
    super();
  }
   
//获得shoucang  list 然后渲染出来
render(){

  //需要判断是否已经登录了
  let arr=this.props.list;
  console.log('shoucang arr Doc:',arr);
  if(arr.length<1){
    return (
      <div>
         <p onClick={()=>{
        this.props.history.goBack();
      }} style={{fontWeight:'bolder'}}>{`<----返回`}</p>
        目前没有收藏商品
     
      </div>
    )
  }
  return(
    <div>
       <p onClick={()=>{
         this.props.history.goBack();
         }} >{`<----返回`}</p>
         {
            arr.map((item,index)=>{
              return(
                   <Flex style={{border:'1px solid black'}}>
                     <Flex.Item>
                     <Flex.Item ><img src={item.img} style={{width:'50%',height:'50%',margin:'0 auto'}}></img></Flex.Item>
                     <Flex.Item>
                       <Flex.Item>
                       <p>{`商品名称:${item.title}`}</p>
                        
                      <button onClick={()=>{
                       //取消收藏
                       console.log('取消收藏成功')
                       this.props.dispatch({
                         type:'DELETER_ITEM',
                         index:index
                       })
                       console.log("取消后的收藏：",this.props.list);
                     }} style={{float:'right',margiTop:'10%' }}>删除</button>
                       <p style={{marginRight:'20px'}}>{`商品价格:${item.price} ￥`}</p>
                     
                       </Flex.Item>
                    
                    
                     </Flex.Item>
                     </Flex.Item>
                    
                     </Flex>
               
              )
            })

         }
    </div>
  
  )
        

}
 
}



const mapStateProps=(state)=>{

  return {
      list:state.shoucang,
      userinfo:state.userinfo
  }
}

export default connect(mapStateProps)(withRouter(Doc));
