import React from "react";
import store from './pages/store';
import {
  //as作用时重命名
  HashRouter as Router,
Switch,
Route,
} from "react-router-dom";
import 'antd-mobile/dist/antd-mobile.css';
import More from './pages/More';
import Details from './pages/Details';
import Seach from './pages/Search'
import Content from './pages/Content';
import Login from './pages/login'

import Doc from './pages/Doc';
import Home from './pages/Home';
import Orderlist from './pages/OrderList';
import {Provider} from 'react-redux';




class  App extends React.Component{

  constructor(){
    super();
    this.state=store.getState();
    console.log("state:",this.state);
  }

  render(){

    return(
      <Provider store={store}>
     <Router>
      <Switch>
      <Route exact path='/' component={Details}></Route>
      <Route  path="/gowuche" component={Doc}></Route>
      <Route path='/orderlist' component={Orderlist}></Route>
      <Route  path='/owner' component={Home}></Route>
      <Route  path='/more' component={More}></Route>
      <Route  path='/search' component={Seach}></Route>
      <Route  path='/content' component={Content}></Route>
      <Route  path='/login' component={Login}></Route>
      <Route component={Details}></Route>
  
   
      </Switch>
      </Router>
      </Provider>
    )

  }
 
}

//connect方法里面的参数可以不用写，默认有
export default  App;

