import React from 'react';
import Mytab from './Mytab';

import { SearchBar, WingBlank } from 'antd-mobile';
import { Flex, WhiteSpace } from 'antd-mobile';
import {connect} from 'react-redux';
import Lunbo from './lunbo';
class Details extends React.Component {
  state = {
    arrs:[]
  }
  componentDidMount() {
  fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
    .then(res=>res.json())
    .then(data=>{
      let arr=data.data;
     
      this.props.dispatch({
        type:'ADD_LIST',
        list:arr
      })
      this.setState({
        arrs:arr
      })    
  
    })
    console.log('datalist:',this.props.datalist);
  }
  changeTo=(imgObj)=>{
    this.props.dispatch({
      type:'CHANGE_ITEM',
      value:imgObj
 })
    this.props.history.push('/content');

  }

 getImage=(idx)=>{
  let arrs=this.props.datalist;
  console.log(arrs);
 
  if(arrs.length<1){
    console.log('1')
    return (
      <div></div>
    )
   }
   else{
   
   for(let i=idx;i<idx+1;i++){
     console.log('idx:',idx);
    return (

      <Flex style={{paddingLeft:'15px',paddingRight:'15px',marginTop:"15px"}}>
       <Flex.Item  onClick={()=>this.changeTo(arrs[i])}>
       <div><img src={arrs[i].img} className="img-responsive"></img></div>
       <p>
         {arrs[i].title}
       </p>
       <p>￥ {arrs[i].price}</p>
       </Flex.Item>

       <Flex.Item  onClick={()=>this.changeTo(arrs[i+1])}>
       <div><img src={arrs[i+1].img} className="img-responsive"></img></div>
       <p>
         {arrs[i+1].title}
       </p>
       <p>￥ {arrs[i+1].price}</p>
       </Flex.Item>
     </Flex>
     )

   }
  }
}
  render() {
    return (
      <div>
        <Lunbo/>
         <div>
      {this.getImage(0)}
      {this.getImage(2)}
      {this.getImage(4)}
      {this.getImage(6)}
      {this.getImage(8)}
    </div>

    <Mytab/>
      </div>
   
    );
  }

}
//获得dispatch方法和state的值
const mapStateProps=(state)=>{

  return {
      data:state.detailsDecer,
      datalist:state.datalist
  }
}

export default connect(mapStateProps)(Details);