import React from 'react';
import srote from './store';


//todo 是一开始的默认值

let todo='';

function detailsDecer(state=todo,action){
    //action里面是存放需要更新的数据或状态
    //更新操作同一个变量
    if(action.type=="CHANGE_ITEM"){
        return [action.value];

    }
    else{
        return state;
    }

}
export default detailsDecer;